import React, { ChangeEvent, useState } from "react";
import "./App.css";
import "@sendbird/uikit-react/dist/index.css";
import SendbirdApp from "@sendbird/uikit-react/App";

const APP_ID = String(process.env.REACT_APP_SENDBIRD_APP_KEY);
// const USER_ID = "yw01124"; // 

function App() {
  const [userId, setUserId] = useState("");
  const [nickname, setNickname] = useState("");
  const [show, setShow] = useState(false);

  const handleId = (e: ChangeEvent<HTMLInputElement>) => {
    const id = e.target.value;
    sessionStorage.setItem("userId", id);
  };

  const handleNickname = (e: ChangeEvent<HTMLInputElement>) => {
    const nick = e.target.value;
    sessionStorage.setItem("nickname", nick);
  };
  const handleShow = () => {
    const id = sessionStorage.getItem("userId");
    const nick = sessionStorage.getItem("nickname");
    console.log(id);
    if (id !== "") {
      setUserId(String(id));
      setNickname(String(nick));
      setShow(true);
    } else {
      alert(" ");
    }
  };
  return (
    <div className="App">
      {!show && (
        <div className="box">
          <label className="label" htmlFor="id">
            ID
          </label>
          <input type="text" className="input" onChange={handleId} id="id" />
          <label className="label" htmlFor="nickname">
            NICKNAME
          </label>
          <input
            type="text"
            className="input"
            onChange={handleNickname}
            id="nickname"
          />
          <button onClick={handleShow}></button>
        </div>
      )}
      {show && (
        <div className="chatbox">
          <SendbirdApp
            appId={APP_ID}
            userId={userId}
            theme="light"
            showSearchIcon={true}
            disableUserProfile={true}
            nickname={nickname}
            stringSet={{
              CHANNEL_LIST__TITLE: "Channel List",
              MESSAGE_INPUT__PLACE_HOLDER: " ?",
              MESSAGE_INPUT__PLACE_HOLDER__DISABLED: "  ",
              MESSAGE_INPUT__PLACE_HOLDER__MUTED: " ",
            }}
          />
        </div>
      )}
    </div>
  );
}

export default App;
